.sidebar{
    flex:3;
    margin:20px;
    padding-bottom:30px;
    background-color: #fdfbfb;
    border-radius: 10PX;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.image{
    height: 200px;
    width: 200px;
}
.sidebaritem{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.sidebartitle{
    margin: 10px;
    padding: 5px;
    width:80%;
    border-top:1px solid #a7a4a4;
    border-bottom:1px solid #a7a4a4;
    line-height: 20px;
    text-align: center;
}
.sidebaritem>img{
    margin-top:15px ;
}
.sidebaritem>p{
    padding: 30px;
}
.sidebarlist{
    list-style: none;
    margin-bottom: 30px;
}
.sidebarlistitem{
    display: inline-block;
    width: 50%;
    margin-top: 15px;
    cursor: pointer;
}
.sidebarsocial{
    margin-top: 15px;
    width: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.sidebaricon{
    font-size: 15px;
    margin-left: 10px;
}