.write{
    padding-top: 50px;
}
.writeimg{
    margin-left: 150px;
    width:70vw;
    height:250px;
    border-radius: 10px;
    object-fit: cover;


}
.writeform{
    position: relative;

}
.writeformgroup{
    margin-left: 150px;
    display: flex;
    align-items: center;
}
.writeicon{
    width:25px;
    height: 25px;
    border-radius: 50%;
    border:1px solid;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    color:rgb(153, 149, 149);
    cursor: pointer;
}
.writeinput{
    font-size: 30px;
    border:none;
    padding: 20px;
    width: 70vw;

}
.writeinput:focus{
    outline: none;
}
.writetext{
    font-size: 20px;
}
.writesubmit{
    position:absolute;
    top: 20px;
    right:50px;
    color: white;
    background-color: teal;
    padding: 10px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    font-size: 16px;
}
